body {
  display: flex;
  flex-direction: column; }

#main-wrp,
#footer,
.sidebar-left #main {
  order: 1; }

#footer {
  margin-top: 110px;
  background: #263238;
  font-size: 15px; }
  #footer .container {
    display: flex;
    justify-content: space-between;
    margin-top: 115px;
    margin-bottom: 100px; }

.counter::before, .counter::after {
  color: #b0bec5; }

.counter::before {
  content: '('; }

.counter::after {
  content: ')'; }

#main-menu {
  background: #263238;
  color: #fff;
  font-size: 14px;
  font-weight: 500; }
  #main-menu ul {
    display: flex; }
  #main-menu li + li {
    margin-left: 42px; }
  #main-menu a {
    line-height: 60px;
    transition: color .2s; }
    #main-menu a:hover {
      color: #8bc34a; }

.product {
  display: flex;
  flex-direction: column; }

.view-line .product {
  flex: 0 0 100%;
  max-width: 100%;
  flex-direction: row; }
  .view-line .product-info {
    flex-grow: 1; }

#taxonomy {
  font-size: 14px;
  font-weight: 500; }
  #taxonomy ul {
    display: flex;
    justify-content: center; }
  #taxonomy li + li {
    margin-left: 6px; }
  #taxonomy a {
    display: inline-block;
    padding: 0 21px;
    line-height: 37px;
    background: #fff;
    color: #263238;
    transition: color .2s, background-color .2s; }
    #taxonomy a.active, #taxonomy a:hover {
      background: #e74c3c;
      color: #fff; }

#more-products {
  background: #f4f6f5;
  margin-top: 40px; }
  #more-products ul {
    display: flex;
    justify-content: center; }
  #more-products li + li {
    margin-left: 12px; }
  #more-products a {
    display: inline-block;
    padding: 0 32px;
    line-height: 52px;
    color: #263238;
    background: #fff;
    transition: color .2s, background-color .2s; }
    #more-products a:hover {
      color: #fff;
      background: #8bc34a; }

#aside .section {
  background: #fff;
  border: 1px solid #d7d7d7;
  padding: 26px 26px 29px; }
  #aside .section + .section {
    margin-top: 30px; }

#aside .title {
  color: #263238;
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 22px;
  border-bottom: 1px solid #e7ecf2; }

.title {
  margin-top: 120px; }
  .title h2 {
    text-align: center;
    font-size: 36px;
    font-weight: 500;
    line-height: 60px;
    color: #263238; }

.row {
  display: flex;
  flex-direction: row;
  margin-top: 61px;
  margin-bottom: 120px;
  /*по макету. Но для симметрии с верхом надо ставить 20px (у footer верхний отступ 110) */ }

.col-4 img,
.col-4 a {
  display: block;
  width: 370px;
  text-align: center; }

.col-4 img {
  height: 270px;
  background: #b5b5b5; }

.col-4 a {
  height: 100px;
  padding-top: 16px;
  padding-bottom: 16px;
  background: #d4d4d4;
  font-size: 24px;
  font-weight: 500;
  line-height: 60px;
  color: #263238; }
  .col-4 a:hover {
    color: #fff;
    background: #8bc34a; }

#footer .title {
  position: relative;
  font-size: 22px;
  color: #fff;
  margin-bottom: 30px;
  padding-bottom: 30px; }
  #footer .title:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 51px;
    height: 3px;
    background-color: #8bc34a; }

#footer .footer-link li + li {
  margin-top: 15px; }

#footer .footer-link i {
  margin-right: 15px;
  font-size: 18px;
  color: #8bc34a; }

#footer .footer-link a {
  color: #90a4ae;
  transition: color .2s; }
  #footer .footer-link a:hover {
    color: #8bc34a; }

#copyright {
  padding: 27px 1em 32px;
  border-top: 1px solid #455a64;
  text-align: center;
  color: #90a4ae; }
